export const query = graphql`
    query {
        joris: file(relativePath: {eq: "people/joris_2_zoom.jpg"}) {
            ...inlineAvatarImage
        }
        thijs: file(relativePath: {eq: "people/thijs_2_zoom.jpg"}) {
            ...inlineAvatarImage
        }

        enschede: file(relativePath: {eq: "office/enschede-chill.jpg"}) {
            childImageSharp {
                fluid(maxWidth: 700) {
                    ...GatsbyImageSharpFluid_withWebp
                }
            }
        }
        amsterdam: file(relativePath: {eq: "office/amsterdam.jpg"}) {
            childImageSharp {
                fluid(maxWidth: 700) {
                    ...GatsbyImageSharpFluid_withWebp
                }
            }
        }
    }
`;
